import store from '../store'

export default {
  data () {
    return {
      open: true,
      comments: null
    }
  },
  created () {
    if (this.kids) {
      store.fetchItems(this.kids, (comments) => {
        this.comments = comments
      }.bind(this))
    }
  }
}


