import store from '../store'

export default {
  route: {
    data ({ to, next }) {
      next({ id: to.params.id })
    }
  },
  data () {
    return {
      id: null,
      item: {},
      pollOptions: null,
      comments: []
    }
  },
  ready () {
    this.update()
  },
  methods: {
    update () {
      store.fetchItem(this.id, (item) => {
        this.item = item
        this.fetchComments()
        if (item.type === 'poll') {
          this.fetchPollOptions()
        }
      }.bind(this))
    },
    fetchComments () {
      store.fetchItems(this.item.kids, (comments) => {
        this.comments = comments
      }.bind(this))
    },
    fetchPollOptions () {
      store.fetchItems(this.item.parts, (options) => {
        this.pollOptions = options
      }.bind(this))
    }
  },
  components: {
    item: require('../components/item.vue'),
    comment: require('../components/comment.vue')
  }
}


