import store from '../store'

export default {
  route: {
    data ({ to, next }) {
      next({ id: to.params.id })
    }
  },
  data () {
    return {
      id: null,
      user: {}
    }
  },
  ready () {
    this.update()
  },
  methods: {
    update () {
      store.fetchUser(this.id, (user) => {
        this.user = user
      }.bind(this))
    }
  }
}


